import styled from 'styled-components';

export const Content = styled.div`
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  
`;

export const VideoBackground = styled.video`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Garante que o vdeo cubra toda a rea */
  z-index: -1; /* Coloca o vdeo atrs do contedo */
`;

export const Search = styled.div`
  z-index: 1; /* Coloca o contedo acima do vdeo */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 7vh;
  width: 75vw;

  /* Responsividade para dispositivos mveis */
  @media (max-width: 768px) {
    width: 135vw; /* Ajusta o width para dispositivos mveis */
  }
`;

export const Result = styled.div`
  z-index: 1; /* Coloca o contedo acima do vdeo */
  background: white;
  padding: 25px;
  border-radius: 10px;
  margin-top: 20px;
  font-size: 12px;
  display:flex;
  align-items: center;
`;
export const ResultInfo =styled.div`
    margin-top: 0;
    margin-bottom: 0.3rem;
    border-bottom: 1px solid #adadad;
`


